import React from 'react';

const Section = ({
  children,
  id,
  className = '',
  fullHeight = false,
}) => {
  const heightClass = fullHeight ? 'min-h-screen' : 'min-h-[600px]';

  return (
    <section
      id={id}
      name={id}
      className={`w-full ${heightClass} py-20 px-4 ${className}`}
    >
      <div className="max-w-6xl mx-auto">
        {children}
      </div>
    </section>
  );
};

export default Section;
