'use client';

import React from 'react';
import Image from 'next/image';
import { Section, Card, Tag } from '../ui';
import { selectedWork } from '../../config/content';

// Simple Project Card component for case study format
const ProjectCard = ({ problem, built, outcome, stack, links, image }) => {
  return (
    <Card hover className="flex flex-col h-full">
      {/* Project Image */}
      {image && (
        <div className="rounded-lg overflow-hidden border border-dark-border mb-6 relative h-48">
          <Image src={image} alt="Project" fill className="object-cover" />
        </div>
      )}

      {/* Problem */}
      <div className="mb-4">
        <h4 className="text-text-tertiary text-sm uppercase tracking-wide mb-2">Problem</h4>
        <p className="text-text-secondary text-sm">{problem}</p>
      </div>

      {/* What I Built */}
      <div className="mb-4">
        <h4 className="text-text-tertiary text-sm uppercase tracking-wide mb-2">Built</h4>
        <p className="text-text-secondary text-sm">{built}</p>
      </div>

      {/* Outcome */}
      <div className="mb-6">
        <h4 className="text-text-tertiary text-sm uppercase tracking-wide mb-2">Outcome</h4>
        <p className="text-text-primary font-medium">{outcome}</p>
      </div>

      {/* Stack */}
      <div className="mb-6">
        <div className="flex flex-wrap gap-2">
          {stack.map((tech, i) => <Tag key={i}>{tech}</Tag>)}
        </div>
      </div>

      {/* Links */}
      {links && Object.keys(links).length > 0 && (
        <div className="flex gap-4 mt-auto">
          {links.appStore && (
            <a href={links.appStore} target="_blank" rel="noopener noreferrer" className="text-text-secondary hover:text-accent transition-smooth text-sm">
              View App â†’
            </a>
          )}
          {links.website && (
            <a href={links.website} target="_blank" rel="noopener noreferrer" className="text-text-secondary hover:text-accent transition-smooth text-sm">
              Website â†’
            </a>
          )}
        </div>
      )}
    </Card>
  );
};

const Work = () => {
  // Filter to only featured/completed projects
  const featuredProjects = selectedWork.filter(p => p.featured);

  return (
    <Section id="work">
      <h2 className="text-h1 font-bold text-text-primary mb-4">Selected Work</h2>
      <p className="text-text-secondary mb-12">Problems I've solved with product engineering</p>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {featuredProjects.map(project => (
          <ProjectCard key={project.id} {...project} />
        ))}
      </div>
    </Section>
  );
};

export default Work;
