import React from 'react';

const Card = ({
  children,
  className = '',
  hover = false,
  ...props
}) => {
  const baseStyles = 'bg-dark-surface border border-dark-border rounded-lg p-6';
  const hoverStyles = hover ? 'hover:border-accent hover:shadow-lg hover:shadow-accent/5 transition-smooth' : '';

  return (
    <div className={`${baseStyles} ${hoverStyles} ${className}`} {...props}>
      {children}
    </div>
  );
};

export default Card;
