'use client';

import React, { useState } from 'react';
import Image from 'next/image';
import KeshavLogo from '../../assets/logos/keshav-logo/logo.png';
import { FaBars, FaTimes } from 'react-icons/fa';
import { socialLinks } from '../../config/content';

const Navbar = () => {
    const [nav, setNav] = useState(false);
    const handleClick = () => setNav(!nav);

    const scrollToSection = (id) => {
        const element = document.getElementById(id);
        if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
            setNav(false); // Close mobile menu after click
        }
    };

    return (
        <div className='fixed w-full h-[60px] flex justify-center items-center px-6 md:px-8 bg-dark-bg/95 backdrop-blur-md border-b border-dark-border text-text-secondary z-50'>

            {/* Centered Logo + Navigation */}
            <div className='hidden md:flex items-center space-x-12'>
                {/* Logo */}
                <div className='flex items-center py-4'>
                    <Image src={KeshavLogo} alt="Logo" width={45} height={45} />
                </div>

                {/* Navigation Links */}
                <ul className='flex space-x-2'>
                    <li className='relative'>
                        <button onClick={() => scrollToSection('home')}
                            className='px-4 py-4 text-sm font-medium tracking-wide hover:text-text-primary transition-smooth cursor-pointer relative group rounded-md hover:bg-dark-surface'>
                            Home
                            <span className='absolute bottom-2 left-4 w-0 h-0.5 bg-accent transition-all duration-300 group-hover:w-[calc(100%-2rem)]'></span>
                        </button>
                    </li>
                    <li className='relative'>
                        <button onClick={() => scrollToSection('work')}
                            className='px-4 py-4 text-sm font-medium tracking-wide hover:text-text-primary transition-smooth cursor-pointer relative group rounded-md hover:bg-dark-surface'>
                            Work
                            <span className='absolute bottom-2 left-4 w-0 h-0.5 bg-accent transition-all duration-300 group-hover:w-[calc(100%-2rem)]'></span>
                        </button>
                    </li>
                    <li className='relative'>
                        <button onClick={() => scrollToSection('capabilities')}
                            className='px-4 py-4 text-sm font-medium tracking-wide hover:text-text-primary transition-smooth cursor-pointer relative group rounded-md hover:bg-dark-surface'>
                            Capabilities
                            <span className='absolute bottom-2 left-4 w-0 h-0.5 bg-accent transition-all duration-300 group-hover:w-[calc(100%-2rem)]'></span>
                        </button>
                    </li>
                    <li className='relative'>
                        <button onClick={() => scrollToSection('experience')}
                            className='px-4 py-4 text-sm font-medium tracking-wide hover:text-text-primary transition-smooth cursor-pointer relative group rounded-md hover:bg-dark-surface'>
                            Experience
                            <span className='absolute bottom-2 left-4 w-0 h-0.5 bg-accent transition-all duration-300 group-hover:w-[calc(100%-2rem)]'></span>
                        </button>
                    </li>
                    <li className='relative'>
                        <button onClick={() => scrollToSection('contact')}
                            className='px-4 py-4 text-sm font-medium tracking-wide hover:text-text-primary transition-smooth cursor-pointer relative group rounded-md hover:bg-dark-surface'>
                            Contact
                            <span className='absolute bottom-2 left-4 w-0 h-0.5 bg-accent transition-all duration-300 group-hover:w-[calc(100%-2rem)]'></span>
                        </button>
                    </li>
                </ul>

                {/* Social Links - Desktop (text links only) */}
                <div className='flex items-center gap-4 pl-6 border-l border-dark-border'>
                    <a href={socialLinks.linkedin} target="_blank" rel="noopener noreferrer"
                        className='text-text-tertiary hover:text-accent transition-smooth text-sm'>
                        LinkedIn
                    </a>
                    <a href={socialLinks.github} target="_blank" rel="noopener noreferrer"
                        className='text-text-tertiary hover:text-accent transition-smooth text-sm'>
                        GitHub
                    </a>
                </div>
            </div>

            {/* Mobile Logo + Hamburger */}
            <div className='md:hidden flex justify-between items-center w-full'>
                <Image src={KeshavLogo} alt="Logo" width={45} height={45} />
                <div onClick={handleClick} className='z-10 cursor-pointer'>
                    {!nav ? <FaBars /> : <FaTimes />}
                </div>
            </div>



            {/* Mobile menu */}
            <ul className={!nav ? 'hidden' : 'absolute top-0 left-0 w-full h-screen bg-dark-bg flex flex-col justify-center items-center'} >
                <li className='py-6 text-4xl'>
                    <button onClick={() => scrollToSection('home')} className='hover:text-text-primary transition-smooth'>
                        Home
                    </button>
                </li>
                <li className='py-6 text-4xl'>
                    <button onClick={() => scrollToSection('work')} className='hover:text-text-primary transition-smooth'>
                        Work
                    </button>
                </li>
                <li className='py-6 text-4xl'>
                    <button onClick={() => scrollToSection('capabilities')} className='hover:text-text-primary transition-smooth'>
                        Capabilities
                    </button>
                </li>
                <li className='py-6 text-4xl'>
                    <button onClick={() => scrollToSection('experience')} className='hover:text-text-primary transition-smooth'>
                        Experience
                    </button>
                </li>
                <li className='py-6 text-4xl'>
                    <button onClick={() => scrollToSection('contact')} className='hover:text-text-primary transition-smooth'>
                        Contact
                    </button>
                </li>

                {/* Social Links - Mobile */}
                <div className='flex flex-col items-center gap-4 mt-8 text-lg'>
                    <a href={socialLinks.linkedin} target="_blank" rel="noopener noreferrer"
                        className='text-text-secondary hover:text-accent transition-smooth'>
                        LinkedIn
                    </a>
                    <a href={socialLinks.github} target="_blank" rel="noopener noreferrer"
                        className='text-text-secondary hover:text-accent transition-smooth'>
                        GitHub
                    </a>
                </div>
            </ul>


        </div>
    )
}

export default Navbar
